/*Charts page  - sample comparison tool*/
#bars #bars-wrapper,
#pca #plots-wrapper,
#stack #stack_wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
  margin-top: 9px; /*add some space between charts and legend*/
}

#bars .rChart {
  float: left;
  overflow: visible !important;
  width: 33%; /*border:1px red solid;*/
}

/* Height of charts is calculated in the R script */
#pca .rChart {
  float: left;
  width: 33%; /*height:500px;*/
  border: 0px red solid;
}

#stack .rChart {
  /*border:1px red solid;*/
}

#heatmap img {
  max-width: 100%;
  height: auto; /*width:850px;*/ /*remove width from there as it should be defined right in the export parameters of R*/
}

/* pipeline chart *******************************/
/*icon*/
.icon_pipeline_v {
  background-color: #e4e4e4;
  border-radius: 3px;
  cursor: pointer;
  color: #676767;
  margin-right: 9px;
  border: 1px solid #dadada;
  padding: 4px 8px;
  position: absolute;
  top: 18px;
  right: 18px;
  opacity: 0.6;
  filter: alpha(opacity=60);
}

.icon_pipeline_v:hover {
  opacity: 1;
  filter: alpha(opacity=100);
}

/*chart*/
.block_wrapper {
  position: sticky;
  top: 0;
  background: rgba(255, 255, 255, 0.9);
  overflow: scroll;
  margin: 0 9px 18px 9px;
  min-height: 17em; /*for qclist text to appear correctly*/
}

.block_container {
  width: 300%; /*was 1109px*/
  float: left;
  border: 0px red solid;
}

section.highlight-step-0 .step0,
section.highlight-step-1 .step1,
section.highlight-step-2 .step2,
section.highlight-step-3 .step3,
section.highlight-step-4 .step4,
section.highlight-step-5 .step5,
.block.step0.highlight,
.block.step1.highlight,
.block.step2.highlight,
.block.step3.highlight,
.block.step4.highlight,
.block.step5.highlight {
  border: 1px #0f6d99 solid;
  background-color: #0f6d99;
  transition: all 0.5s ease;
  color: white;
}

.first_branch,
.mainbranch,
.midbranch,
.lowbranch,
.branch {
  float: left;
}

.mainbranch {
  /*was margin-top:57px;*/
  padding-top: 56px;
  height: 139px;
  border: 0px black solid;
}

.first_branch {
  margin-top: 44px;
}

.branch2 {
  margin-top: 20px;
}

.block,
.block-nt,
.block-lb {
  font-size: 90%;
  padding: 5px;
  display: inline-block;
  text-align: center; /*vertical-align: middle;*/
}

.block,
.block_small {
  position: relative;
  padding: 2px;
  cursor: pointer;
  border: 1px #b4d0f3 solid;
  width: 7.5em;
  height: 7.5em;
  border-radius: 50%;
  line-height: 7.1;
  background-color: #eaf0f9;
  color: #5983b5;
}

.block.small {
  font-size: 67%;
  width: 5em;
  height: 5em;
  line-height: 4.3;
}

.block.medium {
  font-size: 85%;
  width: 50px;
  height: 50px;
}

.block-nt {
  border: 1px #c3c3c3 solid;
  line-height: 3.1;
  background-color: #dbdbdb;
  color: #777;
  margin-left: 9px;
}

.block-lb {
  border: 1px #c3c3c3 solid;
  color: #777;
}

.block-ar {
  padding: 2px;
  border: 0px #b4d0f3 solid;
  color: grey;
  display: inline-block;
}

.block-lb.small {
  font-size: 67%;
  padding: 4px;
}

