/*.pipe_v3 .mainbranch{padding-top:99px;}*/
.pipe_v3 .midbranch {
  margin-top: 69px;
  margin-left: -2px;
}

.pipe_v3 .rotate_f,
.pipe_v3 .rotate_t {
  margin-left: -13px;
}

.pipe_v3 .rotate_f {
  margin-right: 5px;
}

.midbranch_trna {
  margin-top: 27px;
}

.lowbranch_tax {
  margin-top: 9px;
}

/*to show space between circles*/
.pipe_v3 .block {
  line-height: inherit;
  width: 6em;
  height: 6em;
}

.pipe_v3 .block.small {
  line-height: inherit;
  font-size: 67%;
  width: 6em;
  height: 6em;
}

.block .children {
  margin: 40% auto;
}

.block .children_l {
  margin: 25% auto;
}

.block.function {
  border: 1px #dacaec solid;
  background-color: #ece2ff;
  color: #8c7cae;
}

.block.taxon {
  border: 1px #eccaca solid;
  background-color: #faebd7;
  color: #a86363;
}

.qclist {
  position: absolute;
  top: 108px;
  left: 18px;
  color: #777;
  line-height: 1.2;
  width: 286px;
  text-align: left;
}

.qclist ul {
  margin: 0;
  padding: 0;
}
.block div.qclist ul li {
  font-size: 0.8rem;
  margin: 0;
  padding: 0;
}

.arrow_pip {
  display: inline-block;
  position: relative;
  background: grey;
  padding: 2px;
  width: 0px;
  margin-left: 2px;
  margin-right: 2px;
}

.arrow_pip:after {
  left: 100%;
  top: 50%;
  border: solid rgba(0, 0, 0, 0);
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(24, 33, 38, 0);
  border-left-color: grey;
  border-width: 5px;
  margin-top: -5px;
}

.arrow_notip {
  display: inline-block;
  background: grey;
  padding: 2px;
  width: 131px;
  margin-left: 2px;
  margin-right: 2px;
}

.rotate_t {
  -ms-transform: rotate(45deg); /* IE 9 */
  -webkit-transform: rotate(45deg); /* Chrome, Safari, Opera */
  transform: rotate(45deg);
  margin-bottom: 12px;
}

.rotate_f {
  -ms-transform: rotate(-45deg); /* IE 9 */
  -webkit-transform: rotate(-45deg); /* Chrome, Safari, Opera */
  transform: rotate(-45deg); /*margin-left: -22px;*/
  margin-bottom: -12px;
  margin-right: 6px; /*for alignment*/
}

table .highlight {
  background-color: #0f6d99 !important;
  color: White;
  transition: all 0.5s ease;
}
